import { Divider } from '@material-ui/core'
import { useEffect, useState } from 'react'
import styled from 'styled-components'
import { getBUSDBalance, getDividends, getMMTBalance } from '../../utils/contractHelper'
import Web3 from 'web3'
import BigNumber from 'bignumber.js'
import TokenIcon from '../../assets/img/logo.png'
import BusdIcon from '../../assets/img/busd.png'


const StyledInput = styled.input`
    border: 0.6vh #caeea4 solid;
    outline-style: none;
    width: 60%;
    height: 6vh;
    font-size: 4vh;
    :focus{
        border:0.6vh #99CC66 solid;
    }
    :hover{
        border:0.6vh #99CC66 solid;
    }
    :active{
        border:0.6vh #99CC66 solid;
    }

`
const StyledButton = styled.div`
background-color: #99CC66;
padding:0 2vh;
border-radius: 1vh;
display: flex;
align-items: center;
justify-content: center;
cursor: pointer;
:hover{
    color:#FFFF66
}
`

const Dashboard = () => {
    const [searchWord, setSearchWord] = useState('')
    const [mmtBalance, setMmtBalance] = useState('--')
    const [busdBalance, setBusdBalance] = useState('--')
    const checkAddress = async () => {
        const res = await getMMTBalance(searchWord)
        setMmtBalance(new BigNumber(Web3.utils.fromWei(res, 'gwei')).toFormat(0))
        const res2 = await getBUSDBalance(searchWord)
        setBusdBalance(new BigNumber(Web3.utils.fromWei(res2, 'gwei')).toFormat(0))
        
    }
    useEffect(async() => {
        const res3=await getDividends()
        console.log(res3,'res3')
    }, [])

    return (
        <div>
            <div style={{ backgroundColor: '#99cc6642', marginTop: '4vh', padding: '2vh 0', borderRadius: '1vh', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <StyledInput placeholder='Wallet Address' value={searchWord} onChange={(v) => { setSearchWord(v.target.value) }}></StyledInput>
                <StyledButton onClick={checkAddress} style={{ marginLeft: '2vh', height: '6vh' }}>CHECK</StyledButton>
            </div>
            <div style={{ backgroundColor: '#f3f3a5', borderRadius: '1vh', padding: '2vh', display: 'flex', flexWrap: 'wrap', justifyContent: 'center', margin: '4vh 0' }}>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>Your Wallet</div>
                    <div style={{ display: 'flex', alignItems: 'center',justifyContent:'center'}}>
                        <div>{mmtBalance}</div>
                        <img style={{ width: '6vh', height: '6vh' }} src={TokenIcon}></img>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center',justifyContent:'center'}}>
                        <div>{busdBalance}</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                    <Divider style={{ margin: '2vh 0' }}></Divider>
                    <div>Reflected Earned</div>
                    <div style={{ display: 'flex', alignItems: 'center',justifyContent:'center'}}>
                        <div>0.000</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                </div>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>Reward Not Claimed</div>
                    <div style={{ display: 'flex', alignItems: 'center',justifyContent:'center'}}>
                        <div>0.000</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                    {/* <StyledButton style={{ width: '36vh', margin: '2vh auto' }}>CLAIM MANUALLY</StyledButton> */}
                    <div style={{ fontSize: '2vh' }}>Rewards are automatically sent every 60 minutes. It can, however, take longer depending on your holdings and trading volume. Rewards will be triggered once they are big enough to cover the gas fees. If you are a smaller holder it may take from a couple hours to a few days for rewards to appear in your wallet. </div>
                </div>
            </div>
            <div style={{ backgroundColor: '#ff666657', borderRadius: '1vh', padding: '2vh', display: 'flex', flexWrap: 'wrap', justifyContent: 'center', margin: '4vh 0' }}>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>Rewards Baseline</div>
                    <div style={{ display: 'flex', alignItems: 'center',justifyContent:'center'}}>
                        <div>500</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                    <Divider style={{ margin: '2vh 0' }}></Divider>
                    <div>Pool Earned</div>
                    <div style={{ display: 'flex', alignItems: 'center',justifyContent:'center'}}>
                        <div>0.000</div>
                        <img style={{ width: '4vh', height: '4vh' }} src={BusdIcon}></img>
                    </div>
                    <div style={{ fontSize: '2vh', marginTop: '1vh' }}>Each transaction will have 1% fee to the reward pool. When the pool reaches a certain baseline, the reward pool will be triggered as long as there is a purchase behavior. Thus to get all the bonuses from the reward pool at once, the reward baseline will keep increasing after each bonus. (The buyer participation amount is at least 100,000,000)</div>
                </div>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>Last 3 Winners</div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', fontSize: '3vh' }}>
                        <div>address</div>
                        <div>rewards</div>
                        <div>blocks</div>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', fontSize: '3vh', margin: '1vh 0' }}>
                        <div>0xuw..yyyy</div>
                        <div>676</div>
                        <div>3456654345</div>
                    </div>
                </div>
            </div>
            <div style={{ backgroundColor: '#99cc6642', borderRadius: '1vh', padding: '2vh', display: 'flex', flexWrap: 'wrap', justifyContent: 'center', margin: '4vh 0' }}>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>Reward Distributed To Holders</div>
                    <div style={{ margin: '2vh 0' }}>1,000,000,000,000,000</div>
                    <div >Reward Distributed To Buyers</div>
                    <div style={{ margin: '2vh 0' }}>1,000,000,000,000,000</div>
                </div>
                <div style={{ width: '80vh', fontSize: '4vh', wordWrap: 'break-word', wordBreak: 'break-word' }}>
                    <div>$MMT Contract</div>
                    <div style={{ margin: '2vh 0' }}>0x4F1A6FC6A7B65Dc7ebC4EB692Dc3641bE997c2F2</div>
                    <div>$BUSD Contract</div>
                    <div style={{ margin: '2vh 0' }}>0x4F1A6FC6A7B65Dc7ebC4EB692Dc3641bE997c2F2</div>
                </div>
            </div>
        </div>
    )
}

export default Dashboard