
import AppBar from '@material-ui/core/AppBar'
import './index.css'
import Logo from '../../assets/img/logo.png'
import MenuIcon from '../../assets/img/menu.png'
import DashboardIcon from '../../assets/img/dashboard.png'
import HomeIcon from '../../assets/img/home.png'
import { SwipeableDrawer } from '@material-ui/core'
import { useEffect, useState } from 'react'
import styled from 'styled-components'
import { useHistory ,useLocation} from 'react-router-dom'

const NavButton = styled.div`
    background: ${props => props.active ? '#99cc6645' : ''} ;
    display:flex;
    align-items: center;
    text-align: left;
    color:#99CC66;
    margin:1rem;
    padding:0.4rem;
    width:8rem;
    font-size: 1rem;
    border-radius:0.4rem;
    cursor: pointer;
    &:hover{
        border:${props => props.active ? '' : '1px #99CC66 solid'}
    }
`

const Header = () => {
    const [visible, setVisible] = useState(false)
    const [currentTab, setCurrentTab] = useState('home')
    const [action,setAction]=useState('robot-off')
    const history = useHistory()
    const location=useLocation()
    const showMenu=(action,show)=>{
        setAction(action)
        setVisible(show)
    }
    const NavTo = (url) => {
        setCurrentTab(url)
        showMenu('robot-off',false)
        history.push(url)
    }
    useEffect(()=>{
        if(location.pathname.indexOf('home')>=0){setCurrentTab('home')}
        else if(location.pathname.indexOf('dashboard')>=0){setCurrentTab('dashboard')}
    },[location])


    return (
        <AppBar position='sticky'>
            <div className='color3' style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <img onClick={() => { showMenu('robot-on',true) }} style={{ width: '4vh', height: '4vh' ,cursor:'pointer'}} src={MenuIcon}></img>
                <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center' }}>
                    <img onClick={()=>{showMenu('robot-jump',false)}} className={action} style={{ width: '8vh', height: '8vh',cursor:'pointer' }} src={Logo}></img>
                    <div style={{ fontSize: '4vh', fontWeight: 700 }}>Money Machine</div>
                </div>
                <div style={{ opacity: 0.001, width: '0.01vh' }}>App</div>
            </div>
            <SwipeableDrawer
                anchor='left'
                open={visible}
                onClose={() => { showMenu('robot-off',false) }}
                onOpen={() => { }}
            >
                <div>
                    <NavButton onClick={()=>{NavTo('home')}} active={currentTab === 'home'}>
                        <img style={{ width: '1.4rem', height: '1.4rem' }} src={HomeIcon}></img>
                        <div style={{ marginLeft: '1.6rem' }}>Home</div>
                    </NavButton>
                    <NavButton onClick={()=>{NavTo('dashboard')}} active={currentTab === 'dashboard'}>
                        <img style={{ width: '1.4rem', height: '1.4rem' }} src={DashboardIcon}></img>
                        <div style={{ marginLeft: '1.6rem' }}>Dashboard</div>
                    </NavButton>
                </div>
            </SwipeableDrawer>
        </AppBar>
    )
}

export default Header